import React,{Component} from 'react';
import './index.css';
import {hashHistory} from 'react-router'
export default class Home extends Component{
    constructor(props){
        super(props)
    }
    toMime = () =>{
        hashHistory.push({
            pathname: '/mine'
        })
    }
    render(){
        return(
            <div onClick={this.toMime}>home</div>
        )
    }
}


